import React, { lazy, Suspense, useEffect, useState } from 'react';

// IMPORT SOCKET
import socket from '../../../../../io';

// IMPORT UI
const UI = lazy( () => import('./UI') );

const List = () => {

    // FOR STUDENT LIST
    const [ Students, setStudents ] = useState([]);
    // MESSAGE WILL SHOW UNTIL THE STUDENTS LIST BEEN LOADED
    const [ Message, setMessage ] = useState("Please wait...");

    useEffect(
        () => {

            GetStudents();

        }, []
    )

    // GET STUDENT LIST
    const GetStudents = () => {
        
        // SEND REQUEST TO GET STUDENTS
        socket.emit('students/list');
        socket.on(
            'students/list', ( res ) => {

                if ( res.err === null ) // IF NO ERROR
                {
                    setStudents( res.data );
                    setMessage('');
                }else // IF ERROR
                {
                    setMessage(res.err.code);
                }
            }
        )
        

    }

    // WHEN SEARCH A STUDENT
    const onSearch = ( e ) => {

        // EXTRACTING
        const { value } = e.target;

        // SEND REQUEST TO GET STUDENTS
        socket.emit('students/list/search', { body: value });
        socket.on(
            'students/list/search', ( res ) => {

                console.log( res );
                // setStudents( res );

            }
        )

    }

    return (
        <Suspense fallback={ <div>Loading...</div> }>
            <UI
                Students={ Students }
                Message={ Message }
                // * FUNCTIONS
                // WHEN USER SEARCH STUDENT
                onSearch={ onSearch }
            />
        </Suspense>
    )
}

export default List;